<template>
<div class="page subpage" id="detail">
    <app-header></app-header>
    <app-scroll class="content">
        <video id="main-video" ref="video"></video>
        <h1></h1>

        <div class="detail" v-html="detail"></div>

    </app-scroll>
    <div class="tools border-top"></div>
</div>   
</template>

<script>
import {mapState} from 'vuex'
import flvjs from 'flv.js'
export default {
    props: {
        goodsId: [Number, String]
    },
    computed: {
        ...mapState({
            detail: state=>state.goods.goodsDetail && state.goods.goodsDetail.detail,
            videoUrl: state=>state.goods.goodsDetail && state.goods.goodsDetail.videoUrl
        })
    },
    watch: {
        videoUrl(){
            // // 
            if(this.$refs.video){
                this.createPlayer();
            }
        }
    },
    methods: {
        createPlayer(){
            //
            if (flvjs.isSupported()) {
                var videoElement = this.$refs.video;
                var flvPlayer = flvjs.createPlayer({
                    type: 'webm',
                    url: this.videoUrl
                });
                flvPlayer.attachMediaElement(videoElement);
                flvPlayer.load();
                // flvPlayer.play();
            }
        }
    },
    created(){
        // 
        this.$store.dispatch('goods/getGoodsDetailAction', {id: this.goodsId});
    },
    mounted(){
        // 
        if(this.videoUrl){
            this.createPlayer();
        }
        
    },
    beforeDestroy(){
        // 
        this.$store.commit('goods/setGoodsDetail', null);
    }
}
</script>

<style lang="scss" scoped>
#detail{
    .content{
        width: 100%;
        position: absolute;
        top: 44px;
        left: 0;
        bottom: 50px;
        #main-video{
            width: 100%;
        }
    }
    .tools{
        width: 100%;
        height: 50px;
        position: absolute;
        bottom: 0;
        left: 0;
    }

}
</style>
<style>
#detail .detail img{
    width: 100%;
}
</style>


